h2.hform {
  text-align: center;
  font-family: roboto;
  color: #3f1c4d;
}

.espace_form {
  padding: 30px;
  display: -ms-flexbox;
  display: flex;
  border-radius: 5px;
  -ms-flex-pack: center;
  justify-content: space-around;
  margin: 20px auto 15vh auto;
}

.espace_form h1 {
  font-family: roboto;
  color: #3f1c4d;
  text-align: center;
  align-self: center;
}
form.crud {
  display: flex;
  flex-direction: column;
  width: 30vw;
}

form.crud label {
  margin: 0 20px 0 20px;
  font-family: roboto;
}

input,
select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 2px solid #36cdc0;
  border-radius: 4px;
  box-sizing: border-box;
}

input[type="submit"] {
  color: white;

  margin: 8px 0;

  border-radius: 4px;
  cursor: pointer;
}

table {
  border-collapse: collapse;
  border: 1px solid #36cdc0;
}
.liste {
  width: 70vw;

  margin: 20px auto 15vh auto;
}

.liste td {
  padding: 10px;
  border-collapse: collapse;
  border: 1px solid #36cdc0;
}

td .scroll {
  height: 20px;
  overflow-y: scroll;
}
