.bloc_editeur {
  padding: 17px;
  background-color: #3f1c4d;
  margin: 20px 4px 0 4px;
  display: flex;
  flex-direction: flex;

  border-radius: 3px;
  justify-content: center;

  width: auto;
  -webkit-box-shadow: 0px 4px 4px 0 #dcdcdc;
  box-shadow: 0px 4px 4px 0 #dcdcdc;
}
.bloc_editeur:hover {
  background-color: #ffb23f;
}
.bloc_editeur:hover .descriptif_editeur p.nomediteur {
  color: #3f1c4d;
}
.descriptif_editeur p.nomediteur {
  font-family: Staatliches;
  letter-spacing: 2px;
  color: #ffb23f;
  font-size: 27px;
  font-weight: bold;
  justify-content: center;
}

.descriptif_editeur {
  text-align: center;
}
