.listing {
  display: flex;
}
nav.navform {
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 5vh;
  justify-content: center;
  margin-top: 70px;
}

nav.navform ul {
  display: flex;
}
nav.navform li {
  list-style-type: none;
  margin: 5px;
  font-size: 2vh;
  font-family: roboto;
  letter-spacing: 3px;
  color: #3f1c4d;
  cursor: pointer;
  text-transform: uppercase;
}
nav.navform a:hover {
  color: #36cdc0;
}

nav.naform a:active {
  color: #36cdc0;
}
