.espacebloc_content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;

  margin: 7vh auto 0 auto;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.carte_prenom {
  font-family: Staatliches;
  letter-spacing: 2px;
  color: #36cdc0;
  font-size: 52px;
  font-weight: bold;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 56px;
  margin: 0 2vw 0 0;
  display: -ms-flexbox;
  display: flex;
  border-radius: 3px;
  justify-content: center;
  -ms-flex-direction: column;
  flex-direction: column;
  width: 10vw;
  -webkit-box-shadow: 0px 4px 4px 0 #dcdcdc;
  box-shadow: 0px 4px 4px 0 #dcdcdc;
  background-color: #3f1c4d;
  height: 12vh;
  width: auto;
  text-align: center;
}

.carte_activite {
  font-family: roboto;
  letter-spacing: 2px;
  color: #3f1c4d;
  font-size: 15px;
  font-weight: bold;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 15vh;
}

.carte_activite a {
  font-family: roboto;
  color: #3f1c4d;
}
.books_contents {
  width: 60vw;
  -ms-flex-pack: center;
  display: -ms-flexbox;
  display: flex;
  height: auto;
  margin: 0 auto 11vh;
}
.events_content {
  width: 60vw;
  -ms-flex-pack: center;
  display: -ms-flexbox;
  display: flex;

  margin: 0 auto 11vh;
  flex-wrap: wrap;
}

.books_contents img {
  width: 8vw;
}
.books_contents p {
  text-align: center;
  font-family: Staatliches;
  letter-spacing: 2px;
  color: #3f1c4d;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  width: 8vw;
}

.titre_content {
  font-family: Staatliches;
  color: #36cdc0;
  letter-spacing: 5px;
  display: flex;
  width: 52vw;
  margin: 1vh auto 5vh auto;
}

.style_event {
  font-family: Staatliches;
  letter-spacing: 2px;
  font-size: 50px;
  font-weight: bold;
}

.descriptif_event {
  background-color: #36cdc0;
  /* background-repeat: no-repeat; */
  height: auto;
  width: 66px;
  -ms-flex-align: center;
  /* align-items: center; */
  -ms-flex-item-align: baseline;
}

.descriptif_event p {
  font-family: Staatliches;
  color: #3f1c4d;
  font-size: 39px;
  font-weight: bold;
  display: flex;
  flex-direction: row-reverse;
  margin: 0 3px 0 0;
}

@media screen and (max-width: 728px) and (min-width: 320px) {
  .books_contents p {
    text-align: center;
    font-family: Staatliches;
    letter-spacing: 2px;
    color: #3f1c4d;
    flex-wrap: wrap;
    width: 27vw;
  }
  .books_contents img {
    width: 27vw;
  }
}
