footer {
  align-items: center;
  height: 6vh;
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  background-color: #3f1c4d;
}

footer .reseaux {
  color: white;
  font-family: roboto;
  font-size: 21px;
  letter-spacing: 5px;
  margin: 0 0 0 58px;
}

footer img {
  height: 4vh;
  margin: 0 0 0 1vw;
  transition: transform 0.2s;
}

footer img:hover {
  transform: scale(1.2);
}

.admin {
  font-family: roboto;
  letter-spacing: 1px;
  display: flex;
  font-size: 15px;
  margin-left: 54vw;
}

.admin p {
  font-family: roboto;
  color: lightgray;
  margin-left: 1vw;
}

.admin p:hover {
  color: #36cdc0;
}

@media screen and (max-width: 2560px) and (min-width: 1440px) {
  footer {
    align-items: center;
    height: 5vh;
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    background-color: #3f1c4d;
  }

  footer .reseaux {
    color: white;
    font-family: roboto;
    font-size: 21px;
    letter-spacing: 5px;
    margin: 0 0 0 58px;
  }

  footer img {
    height: 3vh;
    margin: 0 0 0 1vw;
    transition: transform 0.2s;
  }

  footer img:hover {
    transform: scale(1.2);
  }

  .admin {
    font-family: roboto;
    letter-spacing: 1px;
    display: flex;
    font-size: 16px;
    margin-left: 58vw;
  }

  .admin p {
    font-family: roboto;
    color: lightgray;
    margin-left: 1vw;
  }

  .admin p:hover {
    color: #36cdc0;
  }
}

@media screen and (max-width: 1440px) and (min-width: 1024px) {
  footer {
    align-items: center;
    height: 5vh;
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    background-color: #3f1c4d;
  }

  footer .reseaux {
    color: white;
    font-family: roboto;
    font-size: 21px;
    letter-spacing: 5px;
    margin: 0 0 0 58px;
  }

  footer img {
    height: 3vh;
    margin: 0 0 0 1vw;
    transition: transform 0.2s;
  }

  footer img:hover {
    transform: scale(1.2);
  }

  .admin {
    font-family: roboto;
    letter-spacing: 1px;
    display: flex;
    font-size: 12px;
    margin-left: 32vw;
  }

  .admin p {
    font-family: roboto;
    color: lightgray;
    margin-left: 1vw;
  }

  .admin p:hover {
    color: #36cdc0;
  }
}
@media screen and (max-width: 1024px) and (min-width: 728px) {
  .admin {
    display: none;
  }
}

@media screen and (max-width: 728px) and (min-width: 320px) {
  footer .reseaux {
    display: none;
  }
  .admin {
    display: none;
  }

  footer {
    justify-content: center;
  }

  footer img:hover {
    transform: none;
  }
}
