header {
  display: flex;
  background-color: black;
  height: 4vh;
  top: 0;
  font-family: roboto;
  color: white;
  -ms-flex-align: center;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 2;
}

header h1 {
  font-size: 2vh;
  font-weight: lighter;
  margin: 0 0 0 1vw;
  color: white;
}

header p {
  margin: 0 0 0 69%;
  letter-spacing: 3px;
}

