.logo {
  width: 30vw;
  margin: 3px auto 0;
  display: -ms-flexbox;
  display: flex;
  padding-top: 41px;
}
figure {
  margin: auto;
}
nav.navigation {
  width: 100%;
  display: flex;
  height: 5vh;
  justify-content: center;
}
nav.navigation li {
  list-style-type: none;
  font-size: 2vh;
  font-family: roboto;

  letter-spacing: 3px;
  color: #3f1c4d;
  cursor: pointer;
  text-transform: uppercase;
}
nav.navigation li:hover {
  color: #36cdc0;
}

nav.navigation .subMenu {
  display: none;
}

nav.navigation .subMenu li a {
  line-height: 21px;
  color: white;
  font-size: 1.8vh;
  padding: 10px 10px 10px 10px;
}

nav.navigation .subMenu li a:hover {
  color: #3f1c4d;
}
nav.navigation li:hover .subMenu {
  display: inline-block;
  position: absolute;
  top: 100%;
  left: 0px;
  background-color: #36cdc0;
  border-radius: 3px;
}

nav.navigation a {
  display: inline-block;
  text-decoration: none;
  color: #3f1c4d;
}
nav.navigation a:hover {
  color: #36cdc0;
}

nav.navigation > ul {
  margin: 0px;
  padding: 0px;
}
nav.navigation > ul::after {
  content: "";
  display: block;
  clear: both;
}

nav.navigation > ul > li {
  float: left;
  position: relative;
  padding: 20px 30px;
}
@media screen and (max-width: 752px) and (min-width: 320px) {
  .logo {
    display: none;
  }
  figure {
    margin: auto;
  }
  nav.navigation {
    width: 100%;
    display: block;
    height: 9vh;
    justify-content: center;
    margin-top: 8vh;
    margin-bottom: 17vh;
  }
  nav.navigation li {
    list-style-type: none;
    font-size: 2vh;
    font-family: roboto;

    letter-spacing: 3px;
    color: #3f1c4d;
    cursor: pointer;
    text-transform: uppercase;
  }

  nav.navigation .subMenu {
    display: none;
  }

  nav.navigation .subMenu li a {
    line-height: 21px;
    color: white;
    font-size: 1.8vh;
    padding: 10px 10px 10px 10px;
  }

  nav.navigation li:hover .subMenu {
    display: inline-block;
    position: absolute;
    top: 100%;
    left: 0px;
    background-color: #36cdc0;
    border-radius: 3px;
  }

  nav.navigation a {
    display: inline-block;
    text-decoration: none;
    color: #3f1c4d;
  }

  nav.navigation > ul {
    margin: 0px;
    padding: 0px;
  }
  nav.navigation > ul::after {
    content: "";
    display: block;
    clear: both;
  }

  nav.navigation > ul > li {
    float: left;
    position: relative;
    padding: 20px 30px;
  }
}
