.current_description {
  width: 52vw;
  margin: 20px auto 0 auto;
  height: auto;
}

.current_description p {
  justify-content: left;
  font-family: roboto;
  line-height: 2;
}
.section {
  display: -ms-flexbox;
  display: flex;
  width: 52vw;
  margin: 5vh auto 10vh auto;
}
.petite_section {
  display: -ms-flexbox;
  display: flex;
  width: 52vw;

  margin: 20px auto 0 auto;
  align-items: center;
}

.petite_section p {
  font-size: 15px;
  font-family: roboto;
}
.livres {
  display: flex;
}
.format_titre {
  font-family: Staatliches;
  color: #36cdc0;
  letter-spacing: 5px;
}

.designraw {
  align-self: center;
  margin-left: 20px;
  width: 22vw;
  border: none;
  height: 3px;
}

.dessins {
  -ms-flex-pack: center;
  justify-content: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
  flex-direction: row;
  margin-bottom: 20vh;
  flex-wrap: wrap;
  width: 50%;
  margin: auto;
  z-index: 0;
  font-family: roboto;
}

.animateurs {
  justify-content: center;
  display: flex;
}
.dessins img {
  height: 25vh;
  margin: 20px 0 0 30px;
}

.photos {
  -ms-flex-pack: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  margin-bottom: 20vh;
  font-family: roboto;
}

.container {
  position: relative;
}

.container_animateurs {
  margin: 0 0 0 19px;
}

.container_animateurs a {
  color: #36cdc0;
}
.container_animateurs a:hover {
  color: #3f1c4d;
}

.overlay {
  position: absolute;
  display: flex;
  flex-direction: column;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 20px 0 0 30px;
  opacity: 0;
  transition: 0.5s ease;
  text-align: center;
  background-color: #36cdc0;
}

.container:hover .overlay {
  opacity: 0.9;
}

.overlay p {
  align-self: center;
  color: #3f1c4d;
  margin: auto;
  font-size: 20px;
  opacity: 2;
  font-family: roboto;
  font-weight: bolder;
}

.logo_sites {
  height: 5vh;
  margin: 0 0 0 2vw;
}
#p_sites {
  font-family: Staatliches;
  font-size: 20px;
}
