.espacelivre {
  justify-content: center;
  display: flex;
  margin-top: 30px;
  margin-bottom: 30px;
  padding-top: 10px;
}
.espacelivre figure {
  width: 20vw;
}
.espacelivre figure img {
  height: 50vh;
}

.descriptiflivre {
  margin-left: 20px;
  line-height: 6vh;
  display: flex;
  flex-direction: column;
  width: 25vw;
}

.descriptiflivre a {
  color: #36cdc0;
}
.descriptiflivre h2 {
  font-family: Staatliches;
  text-align: center;
  font-size: 38px;
}
.descriptiflivre p {
  font-family: roboto;
  font-size: 2vh;
}
