/**********************************************************vignette**********************************************************/
.vignette {
  margin: 35px auto 0 auto;
}

.vignette img {
  width: 46vw;
  margin: auto;
  display: flex;
}
figure {
  margin: 0;
}

/**********************************************************bandeau**********************************************************/
.bandeau {
  background-color: #36cdc0;
  width: 46vw;
  height: 33vh;
  margin: 0 auto;
  -ms-flex-align: center;
  align-items: center;
  display: -ms-flexbox;
  display: flex;
  height: 7vh;
  box-shadow: 0px 4px 4px 0 #dcdcdc;
}

.bandeau h2 {
  color: white;
  font-family: roboto;
  text-transform: uppercase;
  margin-left: 39px;
  font-size: 40px;
  font-weight: normal;
  width: 112vw;
}

.bbnom {
  color: #3f1c4d;
  font-size: 21px;
  letter-spacing: 1px;
  font-weight: 900;
}
.date {
  text-align: end;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -ms-flex-pack: end;

  margin: 0 2% 0 0;
  color: white;
  width: 58vw;
  font-family: roboto;
  font-size: 22px;
}
.date p {
  margin: 0 0 0 0;
}

.purple {
  color: #3f1c4d;
}

/**********************************************************bouton**********************************************************/
.plus {
  display: -ms-flexbox;
  display: flex;
  background: transparent;
  -ms-flex-pack: center;
  justify-content: center;
  width: 11vw;
  padding: 15px;
  border: solid 2px #ffb23f;
  margin: 3vh auto 0 auto;
  font-family: Staatliches;
  font-weight: bold;
  text-transform: uppercase;
  color: #3f1c4d;
  letter-spacing: 5px;
  border-radius: 16px;
  cursor: pointer;
  box-shadow: 0px 4px 4px 0 #dcdcdc;
}

.plus:hover {
  color: #3f1c4d;
  border: solid 2px #36cdc0;
  transition: 0.3s;
}
