.espacebloc {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
  justify-content: center;
  width: 60vw;
  margin: 30px auto 30px auto;
  flex-wrap: wrap;
  padding-bottom: 40px;
}

.bloc:nth-child(odd) {
  background-image: url(./img/logosmallblanc.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  -ms-flex-pack: end;
  justify-content: flex-end;
  margin: 10px 33px 10px 33px;
  height: 22vh;
  width: 11vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.bloc:hover {
  transform: scale(1.1);
}

.bloc a {
  height: 24vh;
  display: flex;
  width: 11vw;
}
.bloc:nth-child(even) {
  background-image: url(./img/logosmall.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  -ms-flex-pack: end;
  justify-content: flex-end;
  margin: 10px 33px 10px 33px;
  height: 22vh;
  width: 11vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}
.descriptif {
  background-color: #36cdc0;
  /* background-repeat: no-repeat; */
  height: auto;
  width: 66px;
  -ms-flex-align: center;
  /* align-items: center; */
  -ms-flex-item-align: baseline;
  /* align-self: baseline; */
  align-self: flex-end;
}
.descriptif p {
  font-family: Staatliches;
  color: #3f1c4d;
  font-size: 27px;
  font-weight: bold;
  display: flex;
  flex-direction: row-reverse;
  margin: 0 3px 0 0;
}

@media screen and (max-width: 728px) and (min-width: 320px) {
  .bloc:nth-child(odd) {
    background-image: url(/static/media/logosmallblanc.dc4555e4.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    justify-content: flex-end;
    margin: 10px 33px 10px 33px;
    height: 22vh;
    width: 49vw;
    display: flex;
    flex-direction: column;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
  }

  .bloc:nth-child(even) {
    background-image: url(./img/logosmall.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    -ms-flex-pack: end;
    justify-content: flex-end;
    margin: 10px 33px 10px 33px;
    height: 22vh;
    width: 49vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-transition: -webkit-transform 0.5s;
    transition: -webkit-transform 0.5s;
    -o-transition: transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
  }
}
