.auteurs {
  display: flex;
  margin-bottom: 20px;
  width: 60vw;
  flex-wrap: wrap;
  justify-content: center;
}
.dessinateurs {
  display: flex;
  width: 60vw;
  flex-wrap: wrap;
  justify-content: center;
}

.auteur_titre {
  width: 100%;
  text-align: center;
  margin-top: 20px;
  font-family: Staatliches;
  color: #36cdc0;
  letter-spacing: 5px;
}

.bloc_auteur {
  padding: 17px;
  background-color: #ffb23f;
  margin: 20px 4px 0 4px;
  display: flex;
  flex-direction: flex;

  border-radius: 3px;
  justify-content: center;
  color: #3f1c4d;
  width: auto;
  -webkit-box-shadow: 0px 4px 4px 0 #dcdcdc;
  box-shadow: 0px 4px 4px 0 #dcdcdc;
}

.bloc_auteur:hover {
  background-color: #3f1c4d;
}

.bloc_auteur:hover .descriptif_auteur p.nomauteur {
  color: #ffb23f;
}
.descriptif_auteur p.nomauteur {
  font-family: Staatliches;
  letter-spacing: 2px;
  color: #3f1c4d;
  font-size: 27px;
  font-weight: bold;
  justify-content: center;
}

.descriptif_auteur {
  text-align: center;
}
