.descriptif_livre img {
  width: 10vw;
  cursor: pointer;
  box-shadow: 0px 4px 4px 0 #dcdcdc;
}

.descriptif_livre p {
  text-align: center;
  font-family: Staatliches;
  letter-spacing: 2px;
  color: #3f1c4d;
  width: 162px;
  margin: auto;
}

.bloc_livre {
  margin: 2px 10px 0 10px;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  -o-transition: transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.bloc_livre:hover {
  transform: scale(0.9);
}
@media screen and (max-width: 728px) and (min-width: 320px) {
  .descriptif_livre img {
    width: 30vw;
    cursor: pointer;
    box-shadow: 0px 4px 4px 0 #dcdcdc;
  }
  .descriptif_livre p {
    font-family: Staatliches;
    letter-spacing: 2px;
    color: #3f1c4d;
    width: 162px;
    text-align: center;
  }
}
