.bloc_content {
  padding: 17px;
  background-color: #3f1c4d;
  margin: 20px 4px 0 4px;
  display: flex;
  border-radius: 3px;
  justify-content: center;
  flex-direction: column;
  width: auto;
  -webkit-box-shadow: 0px 4px 4px 0 #dcdcdc;
  box-shadow: 0px 4px 4px 0 #dcdcdc;
}
.bloc_content:hover {
  background-color: #36cdc0;
}
.bloc_content:hover .descriptif_content p.prenom {
  color: #3f1c4d;
}
.descriptif_content p.prenom {
  font-family: Staatliches;
  letter-spacing: 2px;

  color: #36cdc0;
  font-size: 27px;
  font-weight: bold;
  justify-content: center;
}

.activite {
  color: white;
  font-size: 15px;
  font-family: roboto;
}

.descriptif_content {
  text-align: center;
}
